$breakpoints: (
  desktop: 1600,  // ↓ max   Desktop ~ DesktopFirst
  laptop: 1250,   // ↓ max   laptop  - Desktop   ↑ min
  tablet: 940,    // ↓ max   Tablet  - laptop    ↑ min
  phablet: 768,   // ↓ max   Phablet - Tablet    ↑ min
  phone: 650,     // ↓ max   Phone   - Phablet   ↑ min
  _zero: 0,       //     MobileFirst ~ Phone     ↑ min
);

:root {
  @each $name, $value in $breakpoints {
    --#{$name}: #{$value}px;
  }
}

